.artist-card {
    -ms-flex-preferred-size: 63%;
        flex-basis: 63%;
    margin: 0 auto 1rem;
    position: relative;
    font-family: var(--family);
}

.artist-card:last-child {
    margin: 0 auto;
}

.artist-card figure {
    position: relative;
    z-index: 1;
}

.artist-card figcaption {
    background-color: var(--serch);
    z-index: -1;
    position: relative;
    /*top: -1.05rem;*/
    width: 10.86rem;
    min-height: 5.625rem;
    margin: auto;
    padding: 1rem 0.5rem;
    border-radius: 0.125rem;
    -webkit-box-shadow: 0 0.5rem 0.5rem 0 rgba(0, 0, 0, 0.65),inset 0 0.0625rem 0 0 rgba(255, 255, 255, 0.15);
            box-shadow: 0 0.5rem 0.5rem 0 rgba(0, 0, 0, 0.65),inset 0 0.0625rem 0 0 rgba(255, 255, 255, 0.15);
}

.artist-card__name {
    font-size: var(--mobMedium);
    color: var(--white);
    margin-bottom: 0.5rem;
}

.artist-card__number-albums {
    font-size: var(--mobSmall);
    color: var(--grey);
}

.img-container {
    width: 174px;
    margin: auto;
    position: relative;
}
.card__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 174px;
    height: 100%;
    z-index: 2;
    background-color: rgba(255, 255, 255, 0.6);
    display: none;
}

.artist-card:hover .card__overlay {
    display: block;

}
.svg-container {
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 80%;
}
.card__svg {
    width: 30px;
    height: 30px;
    -webkit-transition: 0.25s;
    -o-transition: 0.25s;
    transition: 0.25s;
    cursor: pointer;
}

.card__svg:hover {
    width: 40px;
    height: 40px;
}


