body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: #000000;
}
ul {
  list-style: none;
}
input, button {
  outline: none;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
  margin: auto;
}


